import React from 'react';

function ContactForm(){
    return (
        <>
            <section id="about-contact" className="wrapper">
                <div className="contact-box panel"><i className="fas fa-envelope-square"></i><p>leonhsu95@gmail.com</p></div>
                <div className="contact-box panel"><i className="fab fa-github"></i><p>leonhsu95</p></div>
            </section>

            <section id="contact" className="link-section responsive">
                <div className="wrapper">
                    <h2 id="contact-heading" className="section-heading">Contact Me</h2>
                    <form id="contact-form" action="send" noValidate="">
                        <div id="success"></div>
                        <fieldset>
                            <p className="form-fields">
                                <label htmlFor="firstName">First Name:</label>
                                <input type="text" name="firstName" id="firstName" className="text-input"></input>
                            </p>
                            <p className="form-fields">
                                <label htmlFor="lastName">Last Name:</label>
                                <input type="text" name="lastName" id="lastName" className="text-input"></input>
                                
                            </p>
                            <p className="form-fields">
                                <label htmlFor="phone">Phone:</label>
                                <input type="text" name="phone" id="phone" className="text-input"></input>
                            </p>
                            <p className="form-fields">
                                <label htmlFor="email">Email:</label>
                                <input type="email" name="email" id="email" className="text-input" placeholder="example@email.com"></input>
                            </p>
                            <p className="form-comment">
                            <label htmlFor="comment" className="textlabel">Comment:</label>
                            <textarea name="comment" id="comment" rows="6"></textarea>
                            </p>
                            <p className="form-submit">
                            <button type="submit" id="submit">Submit</button>
                            </p>
                        </fieldset>
                    </form>
                </div>     
            </section>
        </>    
    )
}

export default ContactForm;